.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  max-width: 700px;
  margin: 0 auto;
  color: #2563eb;
  box-sizing: border-box; /* Ensure padding is included in width */
}

h1 {
  color: #2563eb;
  margin-bottom: 20px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

/* Hide the redundant reset button (now in header) */
.reset-button {
  display: none;
}

@media (max-width: 768px) {
  .game {
    padding: 5px;
    margin: 0 auto;
    width: calc(100% - 5px);
    max-width: calc(100% - 5px);
  }
  
  h1 {
    font-size: 1.5rem;
  }
} 